import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import Draggable from 'react-draggable'; // Both at the same time
import cx from 'classnames';
import styled from 'styled-components';
import _ from 'lodash';

const DraggableHandle = styled.div`
  &.fixed-position {
    transform: none !important;
    cursor: default !important;
  }
`;

const PublisherHandler = ({ children, className, ...props }) => {
  const width = 190;
  const height = 106;
  const top = 46;
  const right = 46;

  return (
    <DIV>
      <div
        style={{
          position: 'absolute',
          top,
          right,
          zIndex: 5,
          width,
          height,
          ...(ctx.apply('videoCallModel.useState', 'myState.videoMute')
            ? {
              height: 0,
            }
            : {}),
          ...(!ctx.apply('videoCallModel.useState', 'myState.collapse')
            ? {
              top: 0,
              right: 0,
              bottom: 0,
              width: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0) / 2,
              height: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0),
            }
            : {}),
        }}
      >
        <Draggable
          axis="both"
          handle=".drag-handle"
          defaultPosition={{ x: 0, y: 0 }}
          position={null}
          grid={[8, 8]}
          scale={1}
          bounds={{
            top: -top,
            right,
            bottom: _.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'height', 0) - top - height,
            left: 0 - (_.get(ctx.apply('LAYOUT.getLayout', 'AdvisorVideoCallStatus'), 'width', 0) - right - width),
          }}
          // onStop={(...args) => {
          //   // store prefer screen postion to local storage and use it for default load
          //   console.log('args', args);
          // }}
          disabled={!ctx.apply('videoCallModel.useState', 'myState.collapse')}
        >
          <DraggableHandle
            className={cx(className, 'w-full h-full drag-handle bg-transparent', {
              'cursor-move': ctx.apply('videoCallModel.useState', 'myState.collapse'),
              'fixed-position': !ctx.apply('videoCallModel.useState', 'myState.collapse'),
            })}
            {...props}
          >
            {children}
          </DraggableHandle>
        </Draggable>
      </div>
    </DIV>
  );
};

export default PublisherHandler;
