import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import cx from 'classnames';
import gstyles from '@vl/gstyles';
import { renderPropsComposer } from '@vl/mod-utils/PropsComposer';
import {
  Rate, Row, Col, Button, Modal
} from 'antd';
import { Avatar } from '@uz/unitz-components-web/Avatar';

import PurchaseFormatter from '@uz/unitz-models/PurchaseModel/formatter';


import IconStatus from '@uz/unitz-components-web/TransactionList/components';

const View8 = () => {
  return (
    <DIV className="paymentConfirm">
      <Modal bodyStyle={{ padding: 0 }} width={600} footer={null} visible={true} closable={false}>
        <section className="py-0 w-150">
          <div className="flex flex-col items-center p-6">
            <h2 className="mb-1 text-xl text-main">{ctx.apply('i18n.t', 'Transaction.Detail.paySuccess')}</h2>
            {renderPropsComposer(
              {
                matcher: (props) => _.get(props, 'status') === 'completed',
                render: (props) => IconStatus.completed({ name: _.get(props, 'name') }),
              },
              {
                matcher: (props) => _.get(props, 'status') === 'pending',
                render: (props) => IconStatus.pending({ name: _.get(props, 'name') }),
              },
              {
                matcher: (props) => _.get(props, 'status') === 'reject',
                render: (props) => IconStatus.reject({ name: _.get(props, 'name') }),
              },
              () => IconStatus.unknown({ name: ctx.apply('i18n.t', `Transaction.Status.${ctx.get('@items.status')}`) })
            )({
              status: ctx.get('@items.status'),
              name: ctx.apply('i18n.t', `Transaction.Status.${ctx.get('@items.status')}`),
            })}
            <div className="flex mt-2">
              <p className="mb-0 mr-2 text-4xl text-main">{ctx.get('@items.bill.totalAfter')}</p>
            </div>
          </div>
          <div className="border-b border-divider"></div>
          <Row align="middle" className="p-6">
            <Col span={4}>
              <Avatar size={80} src={ctx.get('@items.advisorProfile.avatarUrl')} />
            </Col>
            <Col span={1}></Col>
            <Col span={12}>
              <h1 className="mb-0 text-xl font-medium">
                {PurchaseFormatter.advisorName(ctx)(ctx.get('purchaseData'))}
              </h1>
              <div className="flex items-center mb-1">
                <div className="mr-2 text-base">{ctx.get('@items.advisorProfile.rating.ratingText')}</div>
                <Rate disabled count={5} value={ctx.get('@items.advisorProfile.rating.ratingScore')} />
              </div>
              <div className="uppercase text-sub text-xxs">
                {ctx.get('@items.advisorProfile.reviewCount')} {ctx.apply('i18n.t', 'Payment.Profile.reviews')}
              </div>
            </Col>
            <Col span={7}>
              <Button
                type="primary"
                disabled={ctx.get('@items.status') !== 'completed'}
                onClick={() => ctx.apply('@items.startService')}
              >
                {gstyles.icons({
                  name: 'videocam',
                  size: 20,
                  fill: gstyles.colors.white500,
                  className: 'inline-block mr-2',
                })}
                <span className="text-white500">{ctx.apply('i18n.t', 'Transaction.Detail.call')}</span>
              </Button>
            </Col>
          </Row>
          <div className="border-b border-divider"></div>
          <div className={cx({ 'hidden ': !ctx.get('@items.show') }, 'flex p-6 flex-col bg-white200')}>
            <h2 className="text-xl">{ctx.apply('i18n.t', 'Transaction.Detail.paymentDetail')}</h2>
            <div className="flex justify-between">
              <span className="text-base text-sub">
                {ctx.apply('i18n.t', 'UserPayment.serviceType', { kind: ctx.get('@items.service.kind') })}
              </span>
              <span className="text-base text-main">{ctx.get('@items.service.name')}</span>
            </div>
            <div className="my-6 border-b border-divider"></div>
            <div className="flex justify-between mb-3">
              <span className="text-base text-sub">{ctx.apply('i18n.t', 'Payment.Bill.tempCost')}</span>
              <span className="text-base text-main">{ctx.get('@items.bill.totalBefore')}</span>
            </div>
            {ctx.get('@items.bill.discount') && (
              <div className="flex justify-between">
                <span className="text-base text-sub">{ctx.apply('i18n.t', 'Payment.Bill.promotion')}</span>
                <span className="text-base text-main">-{ctx.get('@items.bill.discount')}</span>
              </div>
            )}
            <div className="my-6 border-b border-divider"></div>
            {/* <div className="flex justify-between">
              <span className="text-base text-sub">{ctx.apply('i18n.t', 'Payment.Bill.unitzCoin')} (500.000 c)</span>
              <span className="text-base text-main">
                -{PurchaseFormatter.fromMoney(ctx)(ctx.get('purchaseData'), 'wallet')}
              </span>
            </div>
            <Divider /> */}
            <div className="flex justify-between">
              <span className="text-base font-semibold text-main">{ctx.apply('i18n.t', 'Payment.Bill.totalCost')}</span>
              <span className="text-base text-branda">{ctx.get('@items.bill.totalAfter')}</span>
            </div>
          </div>
          <div className="border-b border-divider"></div>
          {/* eslint-disable-next-line */}
          <label
            className="flex flex-row items-center justify-center py-4 text-brandb"
            onClick={() => {
              ctx.apply('@items.$show', !ctx.get('@items.show'));
            }}
          >
            <span className="mr-2">
              {ctx.get('@items.show') ? ctx.apply('i18n.t', 'Advisor.less') : ctx.apply('i18n.t', 'Advisor.more')}
            </span>
            {ctx.get('@items.show')
              ? gstyles.icons({ name: 'arrowUp', size: 12, fill: gstyles.colors.brandb500 })
              : gstyles.icons({ name: 'arrowDown', size: 12, fill: gstyles.colors.brandb500 })}
          </label>
        </section>
      </Modal>
    </DIV>
  );
};

export default View8;
