import React from 'react';
import _ from 'lodash';

import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import { resable } from '@vl/redata/RESABLE';
import RESABLE from '@vl/redata/RESABLE.macro';
import PaymentConfirm from '@uz/unitz-components-web/PaymentConfirm';

import { Layout } from 'antd';

const { Content } = Layout;
const Index = () => {
  return (
    <DIV className="pageContainer">
      <Layout className="py-0 bg-transparent ">
        <Content>
          <RESABLE>
            <DIV forceCtx>
              {ctx.debug(() => {
                const purchaseData = resable.for('purchaseData')(ctx.get('purchaseData'), []);
                ctx.set('purchaseData', _.get(purchaseData, '0'));
              })}
              <PaymentConfirm />
            </DIV>
          </RESABLE>
        </Content>
      </Layout>
    </DIV>
  );
};

export default Index;
