import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import gstyles from '@vl/gstyles';
import cx from 'classnames';
import { Dropdown } from 'antd';

const MenuSetting = ({ data }) => {
  return (
    <div className="flex flex-col w-48 py-2 rounded-lg mb-20px bg-white500">
      {data
        && data.map(({ icon = '', name = '', ...rest }, index) => (
          // eslint-disable-next-line
          <div key={`${index}`} className="flex px-3 py-2 cursor-pointer" {...rest}>
            {icon}
            <span className="text-main">{name}</span>
          </div>
        ))}
    </div>
  );
};

const ControlButton = ({
  className, icon, text, ...rest
}) => {
  return (
    <div {...rest} className={`flex items-center justify-center cursor-pointer flex-col ${className}`}>
      {icon}
      {!!text && <div className="mb-0 font-light text-normal text-white500">{text}</div>}
    </div>
  );
};

const View8 = (props) => {
  return (
    <DIV className="videoCallConnecting">
      <section className="absolute bottom-0 z-10 flex justify-between w-full px-6 py-4 bg-black400 bg-opacity-70">
        <div className="relative flex justify-between w-36">
          <Dropdown
            visible={ctx.get('@sectionActions.visibleDropdown')}
            overlay={
              <MenuSetting
                data={[
                  {
                    onClick: () => {
                      ctx.apply('videoCallModel.setState', 'myState.collapse', false);
                      ctx.apply('@sectionActions.$visibleDropdown', !ctx.get('@sectionActions.visibleDropdown'));
                    },
                    icon: gstyles.icons({
                      name: 'split-screen',
                      size: 22,
                      fill: gstyles.colors.sub,
                      className: 'mr-3',
                    }),
                    name: ctx.apply('i18n.t', 'VideoCallPage.SectionAction.split'),
                  },
                  {
                    onClick: () => {
                      ctx.apply('videoCallModel.setState', 'myState.collapse', true);
                      ctx.apply('@sectionActions.$visibleDropdown', !ctx.get('@sectionActions.visibleDropdown'));
                    },
                    icon: gstyles.icons({
                      name: 'pic-in-pic',
                      size: 22,
                      fill: gstyles.colors.sub,
                      className: 'mr-3',
                    }),
                    name: ctx.apply('i18n.t', 'VideoCallPage.SectionAction.collapse'),
                  },
                ]}
              />
            }
            placement="topLeft"
            trigger={['click']}
          >
            <ControlButton
              onClick={() => {
                ctx.apply('@sectionActions.$visibleDropdown', !ctx.get('@sectionActions.visibleDropdown'));
              }}
              icon={gstyles.icons({
                name: 'setting',
                fill: gstyles.colors.white500,
                size: 24,
                className: 'mb-2',
              })}
              text={ctx.apply('i18n.t', 'VideoCallPage.SectionAction.setting')}
            />
          </Dropdown>
        </div>
        <div className="flex justify-between w-52">
          <ControlButton
            className="w-12 h-12 rounded-full bg-white200 bg-opacity-30"
            onClick={() => {
              ctx.apply('videoCallModel.toggleMic');
            }}
            icon={gstyles.icons({
              name: ctx.apply('videoCallModel.useState', 'myState.micMute') ? 'micro-off' : 'micro',
              fill: gstyles.colors.white500,
              size: 24,
            })}
          />
          <ControlButton
            onClick={() => {
              ctx.apply('videoCallModel.toggleVideo');
            }}
            className={cx(
              'w-12 h-12 rounded-full',
              { 'bg-white500': ctx.apply('videoCallModel.useState', 'myState.videoMute') },
              { 'bg-white200 bg-opacity-30': !ctx.apply('videoCallModel.useState', 'myState.videoMute') }
            )}
            icon={gstyles.icons({
              name: ctx.apply('videoCallModel.useState', 'myState.videoMute') ? 'videocam-off' : 'videocam',
              fill: gstyles.colors.white500,
              size: 24,
            })}
          />
          <ControlButton
            onClick={() => {
              ctx.apply('videoCallModel.endCall');
            }}
            className="w-12 h-12 rounded-full bg-red "
            icon={gstyles.icons({ name: 'call-end', fill: gstyles.colors.white500, size: 24 })}
          />
        </div>
        <div className="flex justify-between w-36">
          {/* <ControlButton
            icon={gstyles.icons({
              name: 'add-note',
              fill: gstyles.colors.white500,
              size: 24,
              className: 'mb-2',
            })}
            text={ctx.apply('i18n.t', 'VideoCallPage.SectionAction.note')}
          />
          <ControlButton
            icon={gstyles.icons({
              name: 'heart',
              fill: gstyles.colors.white500,
              size: 24,
              className: 'mb-2',
            })}
            text={ctx.apply('i18n.t', 'VideoCallPage.SectionAction.like')}
          /> */}
        </div>
      </section>
    </DIV>
  );
};

export default View8;
