import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import gstyles from '@vl/gstyles';

export const View8 = () => {
  return (
    <DIV className="paymentWallet">
      <section className="p-6 mb-6 border border-border rounded-xl">
        <h2 className="mb-6 text-xl text-main">{ctx.apply('i18n.t', 'Payment.StepTwoTitle')}</h2>
        <div
          className="flex items-center cursor-pointer"
          onClick={() => {
            ctx.apply('form.togglePaymentMethod', { type: 'wallet' });
          }}
        >
          {gstyles.icons({
            name: ctx.apply('form.hasPaymentMethod', { type: 'wallet' }) ? 'check-box-on' : 'check-box-off',
            size: 18,
            className: 'mr-2',
          })}
          <p className="mb-0 mr-1 text-base font-normal text-main">
            {ctx.apply('i18n.t', 'UserPayment.payment')}: {ctx.get('@item.walletAmount')}
          </p>
          <span className="text-base font-normal text-sub">
            (
            {`${ctx.apply('i18n.t', 'AdvisorWallet.walletBalance')} ${ctx.get('wallet.amountAvailable')} ${ctx.get(
              'wallet.currency'
            )}`}
            )
          </span>
        </div>
        {!!ctx.get('form.errors.creditId') && (
          <div className="text-xs text-red500">{ctx.get('form.errors.creditId')}</div>
        )}
      </section>
    </DIV>
  );
};
export default View8;
