// import React from 'react';
import { bindings, hook } from '@vl/redata';

import { formatVND } from '@vl/mod-utils/currencyFormatter';

const bindData = bindings({
  paymentBill: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              const formValues = ctx.get('form.values') || {};
              const {
                totalAfter, totalBefore, discount, service
              } = formValues;
              const result = {
                service,
                bill: {
                  totalAfter: totalAfter ? formatVND(totalAfter) : 0,
                  totalBefore: totalBefore ? formatVND(totalBefore) : 0,
                  discount: discount ? formatVND(discount) : 0,
                },
              };
              return result;
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
