import { bindings, hook } from '@vl/redata';

import { formatVND } from '@vl/mod-utils/currencyFormatter';

const bindData = bindings({
  paymentWallet: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              return {
                walletAmount: formatVND(ctx.get('form.values.payments[0].amount')),
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
