import { bindings, hook } from '@vl/redata';

const bindData = bindings({
  paymentMethods: {
    rules: [
      [
        'data',
        {
          data: {
            '@item': hook((ctx) => {
              const banks = (ctx.get('banks') || []).map((bank) => ({
                ...bank,
                value: bank.bank_code,
                label: bank.name,
              }));
              const lists = [
                {
                  id: 'token',
                  label: 'My cards',
                  type: 'token',
                  banks,
                },
                {
                  id: 'payment',
                  label: ctx.apply('i18n.t', 'Payment.Method.domesticDebitCard'),
                  type: 'payment',
                  banks,
                },
                {
                  id: 'momo',
                  label: 'Momo Wallet',
                  type: 'momo',
                  banks,
                },
              ];
              return {
                lists,
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
