import React from 'react';
import { bindings, hook } from '@vl/redata';

import useObservableSource from '@vl/hooks/useObservableSource';

import useRoute from '@vl/hooks/useGbRoute';
import PurchaseModel from '@uz/unitz-models/PurchaseModel';

import _ from 'lodash';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            '@page': hook(() => {
              const [showDetail, $showDetail] = React.useState(true);
              const [transactionDetail, $transactionDetail] = React.useState({});
              return {
                showDetail,
                $showDetail,
                transactionDetail,
                $transactionDetail,
                onClickRow: (record) => {
                  $transactionDetail(record);
                  $showDetail(true);
                },
              };
            }),
            purchaseData: hook(() => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const purchaseId = _.get(routeParams, 'purchase_id');
              const items = useObservableSource(() => PurchaseModel.find(
                `where: {id: {_eq: "${purchaseId}"}}`,
                `
                    id
                    payments {
                      amount
                      type
                    }
                    services {
                      id
                      price_amount
                      advisor_service {
                        id
                        service_id
                        advisor {
                          id
                          profile {
                            display_name
                            avatar_url
                          }
                          review_agg {
                            avg_score
                          }
                          reviews_aggregate {
                            aggregate { count }
                          }
                        }
                        service { id name kind }
                        price {
                          price_amount
                          price_currency
                          per_unit
                          per_amount
                        }
                        discounts {
                          percentage
                          currency
                          id
                          amount
                          type
                        }  
                      }
                    }
                    status {
                      status
                    }
                    status_latest {
                      status
                    }
                  `
              ));
              return items;
            }),
          },
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
