import React from 'react';
import { bindings, hook } from '@vl/redata';

import _ from 'lodash';
import AdvisorProfileFormatter from '@uz/unitz-models/AdvisorProfileModel/formatter';
import UserWalletModelFormatter from '@uz/unitz-models/UserWalletModel/formatter';
import PurchaseModelFormatter from '@uz/unitz-models/PurchaseModel/formatter';
import CFAdvisorProfileFormatter from '@uz/unitz-models/CFAdvisorProfileModel/formatter';

import AdvisorModel from '@uz/unitz-models/AdvisorModel';
import UserWalletModel from '@uz/unitz-models/UserWalletModel';
import BankModel from '@uz/unitz-models/BankModel';
import CardWalletModel from '@uz/unitz-models/CardWalletModel';

import useObservableSource from '@vl/hooks/useObservableSource';
import fbFnsClient from '@vl/mod-clients/fibGatsbyFns';

import useRoute from '@vl/hooks/useGbRoute';
import usePromiseSource from '@vl/hooks/usePromiseSource';

import { useFormik } from 'formik';
import * as Yup from 'yup';

const formRef = {
  prevValues: {},
  currValues: {},
  addValues: (val) => {
    const prevValues = _.cloneDeep(val);
    formRef.currValues = _.cloneDeep(val);
    setTimeout(() => {
      formRef.prevValues = prevValues;
    });
  },
  isChanged: (...args) => {
    const [dataPath, toVal] = args;
    const prevVal = _.get(formRef.prevValues, dataPath);
    const newVal = _.get(formRef.currValues, dataPath);
    const isChanged = !_.isEqual(prevVal, newVal);
    if (args.length === 2) {
      return isChanged && _.isEqual(toVal, newVal);
    }
    return isChanged;
  },
};

const paymentFormReducer = (ctx) => (formValues) => {
  formRef.addValues(formValues);

  const { serviceId } = formValues;

  const service = ctx.get('advisor.packages').find((item) => item.service_id === serviceId);
  const serviceAmount = CFAdvisorProfileFormatter.amountPayment(ctx)(service);

  const walletAmountAvailable = ctx.get('wallet.amountAvailableValue');

  const paymentMethodWalletIndex = _.findIndex(_.get(formValues, 'payments'), ({ type }) => type === 'wallet');
  const paymentMethodWallet = _.get(formValues, `payments.${paymentMethodWalletIndex}`);
  const paymentMethodCardIndex = _.findIndex(_.get(formValues, 'payments'), ({ type }) => type === 'payment');
  const paymentMethodCard = _.get(formValues, `payments.${paymentMethodCardIndex}`);
  const paymentMethodTokenIndex = _.findIndex(_.get(formValues, 'payments'), ({ type }) => type === 'token');
  const paymentMethodToken = _.get(formValues, `payments.${paymentMethodTokenIndex}`);

  // update wallet payment
  if (serviceId && paymentMethodWallet) {
    _.update(paymentMethodWallet, 'amount', (value) => {
      if (paymentMethodWallet.selected) {
        const amount = value || _.min([walletAmountAvailable, serviceAmount.totalAfter]);
        return amount;
      }
      return 0;
    });
  }

  // is newly selected
  _.map([paymentMethodCardIndex, paymentMethodTokenIndex], (paymentMethodIndex) => {
    if (formRef.isChanged(`payments.${paymentMethodIndex}.selected`, true)) {
      // update the wallet pament method if needed
      if (paymentMethodWallet.selected) {
        _.update(paymentMethodWallet, 'amount', (value) => {
          if (value >= serviceAmount.totalAfter) {
            paymentMethodWallet.selected = false;
            return 0;
          }
          return value;
        });
      }
    }
  });

  const totalAfterWallet = serviceAmount.totalAfter - paymentMethodWallet.amount;

  // update card payment
  _.map([paymentMethodCard, paymentMethodToken], (paymentMethod) => {
    if (paymentMethod) {
      _.update(paymentMethod, 'amount', () => {
        if (paymentMethod.selected) {
          return totalAfterWallet;
        }
        return 0;
      });
      // de-select paymentMethod if totalAfterWallet zero
      if (!totalAfterWallet) {
        paymentMethod.selected = false;
      }
    }
  });

  const newValues = {
    ...formValues,
    payments: _.compact([paymentMethodWallet, paymentMethodCard, paymentMethodToken]),
    service,
    walletAmountAvailable,
    ...serviceAmount,
  };

  newValues.paidAmount = _.sumBy(newValues.payments, ({ amount, selected }) => (selected && amount) || 0);
  newValues.isFullPaid = newValues.paidAmount >= newValues.totalAfter;

  return newValues;
};

const paymentFormValidation = (ctx) => () => {
  return Yup.object().shape({
    serviceId: Yup.string().required(ctx.apply('i18n.t', 'Payment.Error.service')),
    payments: Yup.array().of(
      Yup.lazy((object) => {
        const type = _.get(object, 'type');
        // validate wallet payment
        if (type === 'wallet') {
          return Yup.object();
        }
        // validate card payment
        if (type === 'payment' && !_.get(object, 'bank_code') && _.get(object, 'selected')) {
          return Yup.object().shape({
            bank_code: Yup.string().required(ctx.apply('i18n.t', 'Payment.Error.bank')),
          });
        }
        // validate token payment
        if (type === 'token' && _.get(object, 'selected')) {
          return Yup.object().shape({
            bank_code: Yup.string()
              .required(ctx.apply('i18n.t', 'Payment.Error.bank'))
              .oneOf([true], 'Not support'),
          });
        }
        return Yup.object();
      })
    ),
    isFullPaid: Yup.mixed().oneOf([true], ctx.apply('i18n.t', 'Payment.Error.invalidPayments')),
  });
};

const bindData = bindings({
  paymentPage: {
    rules: [
      [
        'data',
        {
          data: {
            advisorId: hook(() => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const advisorId = _.get(routeParams, 'id', '');
              return advisorId;
            }),
            cards: hook((ctx) => {
              const userId = ctx.apply('authModel.getUserId');
              const items = useObservableSource(() => CardWalletModel.find(
                `where: {user_id: {_eq: "${userId}"}} limit: 20`,
                'id user_id is_default card_number bank_code card_type'
              ));
              return items;
            }),

            banks: hook(() => {
              const items = usePromiseSource(
                async () => {
                  try {
                    const items = await BankModel.find('', 'id name bank_code logo_url');
                    return items.toObject();
                  } catch (err) {}
                },
                [],
                []
              );
              return items;
            }),
            advisor: hook((ctx) => {
              const advisorId = ctx.get('advisorId');
              const advisor = usePromiseSource(
                async () => {
                  try {
                    const item = await AdvisorModel.find(
                      `where: {id: {_eq: "${advisorId}"}}`,
                      `
                      services(order_by: {price: {price_amount: asc}}) {
                        price {
                          per_unit
                          per_amount
                          price_currency
                          price_amount
                        }
                        id
                        service { id name kind }
                        discounts {
                          percentage
                          currency
                          id
                          amount
                          type
                        }
                      }
                      profile { id, display_name, avatar_url, ref_ctf_eid, advisor_id, created_at }
                      review_agg { avg_score }
                      reviews_aggregate {
                        aggregate { count }
                      }
                      `
                    );
                    return item.toObject();
                  } catch (err) {}
                },
                {},
                []
              );
              const route = useRoute();
              const profile = _.get(advisor, '0.profile');
              const services = _.get(advisor, '0.services', []);
              const routeParams = _.get(route, 'params', {});
              const service_kind = _.get(routeParams, 'service_kind');
              const reviewAggregate = _.get(advisor, '0.reviews_aggregate.aggregate');
              const item = _.get(advisor, '0', []);
              const result = {
                profile: {
                  id: AdvisorProfileFormatter.profileId(ctx)(profile),
                  displayName: AdvisorProfileFormatter.displayName(ctx)(profile),
                  avatarUrl: AdvisorProfileFormatter.avatarUrl(ctx)(profile),
                },
                services: AdvisorProfileFormatter.services(ctx)(services),
                packages: AdvisorProfileFormatter.packages(ctx)(services, service_kind),
                rating: {
                  ratingText: AdvisorProfileFormatter.reviewText(ctx)(item),
                  ratingScore: AdvisorProfileFormatter.reviewScore(ctx)(item),
                },
                reviewCount: AdvisorProfileFormatter.reviewCount(ctx)(reviewAggregate),
              };
              return result;
            }),
            wallet: hook((ctx) => {
              const userId = ctx.apply('authModel.getUserId');
              const walletData = usePromiseSource(
                async () => {
                  try {
                    const items = await UserWalletModel.find(
                      `where: {user_id: {_eq: "${userId}"} wallets: { wallet_type: {_eq: credit}}}`,
                      `
                    wallets {
                      id
                      name
                      wallet_type
                      balance {
                        amount
                        amount_available
                        id
                      }
                    }
                    id
                  `
                    );
                    return items.toObject();
                  } catch (err) {}
                },
                {},
                []
              );
              const wallets = _.get(walletData, '0.wallets');
              const result = {
                id: UserWalletModelFormatter.id(ctx)(wallets),
                amountAvailable: UserWalletModelFormatter.available(ctx)(wallets),
                amountAvailableValue: UserWalletModelFormatter.availableValue(ctx)(wallets),
                amount: UserWalletModelFormatter.amount(ctx)(wallets),
                currency: '',
              };
              return result;
            }),
            form: hook((ctx) => {
              const route = useRoute();
              const initialValues = React.useMemo(
                () => paymentFormReducer(ctx)({
                  serviceId: '',
                  promotion: '',
                  payments: [
                    { type: 'wallet', amount: 0, selected: false },
                    {
                      type: 'token',
                      amount: 0,
                      selected: false,
                      bank_code: '',
                      return_url: route.redirectUrl({ step: 'confirm' }),
                      cancel_url: route.redirectUrl({ step: 'cancel' }),
                    },
                    {
                      type: 'payment',
                      amount: 0,
                      selected: false,
                      bank_code: '',
                      return_url: route.redirectUrl({ step: 'confirm' }),
                      cancel_url: route.redirectUrl({ step: 'cancel' }),
                    },
                    {
                      type: 'momo',
                      amount: 0,
                      selected: false,
                      bank_code: '',
                      return_url: route.redirectUrl({ step: 'confirm' }),
                      cancel_url: route.redirectUrl({ step: 'cancel' }),
                    },
                  ],
                  totalAmount: 0,
                  advisorId: ctx.get('advisorId'),
                }),
                []
              );

              const form = useFormik({
                initialValues,
                onSubmit: async (values) => {
                  const userId = ctx.apply('authModel.getUserId');
                  const sessionInputs = PurchaseModelFormatter.normalizePaymentForm(ctx)({
                    user_id: userId,
                    advisor_id: values.advisorId,
                    service_id: values.serviceId,
                    ...values,
                  });
                  const data = await fbFnsClient.getClient().post('purchase-createSession', {
                    ...sessionInputs,
                  });

                  if (_.get(data, 'code') === '00') {
                    const paymentUrl = _.get(data, 'data');
                    if (paymentUrl) {
                      // redirect payment form
                      route.navigate(paymentUrl);
                    } else {
                      // move to next step confirm payment
                      const purchase_id = _.get(data, 'id');
                      const step = 'confirm';
                      const toUrl = route.redirectUrl({ step, purchase_id }, { origin: false });
                      route.navigate(toUrl, {
                        relace: true,
                        state: { purchase_id },
                      });
                    }
                    return false;
                  }
                },
                validationSchema: paymentFormValidation(ctx)(),
              });

              const paymentMethods = ['payment', 'token', 'momo'];

              // add helpers to payment form
              _.assign(form, {
                togglePaymentMethod: (item) => {
                  const payments = [...ctx.get('form.values.payments', [])];
                  payments.map((payment) => {
                    if (payment.type === item.type) {
                      payment.selected = !payment.selected;
                    }
                  });
                  ctx.apply('form.setFieldValue', 'payments', payments);
                },
                hasPaymentMethod: (item) => {
                  const payments = ctx.get('form.values.payments', []);
                  const found = _.find(payments, (payment) => !!payment.selected && payment.type === item.type);
                  return !!found;
                },
                selectPaymentMethod: (item) => {
                  const payments = [...ctx.get('form.values.payments', [])];
                  payments.map((payment) => {
                    if (paymentMethods.includes(payment.type)) {
                      payment.selected = false;
                      if (payment.type === item.type) {
                        payment.selected = true;
                      }
                    }
                  });
                  ctx.apply('form.setFieldValue', 'payments', payments);
                },
              });

              React.useEffect(() => {
                const formValues = paymentFormReducer(ctx)(form.values);
                form.setValues(formValues);
              }, [JSON.stringify({ values: form.values })]);
              return form;
            }),

            canSubmit: hook((ctx) => {
              return ctx.get('form.isValid') && !ctx.get('form.isSubmitting') && ctx.get('form.dirty');
            }),

            paymentPage: hook((ctx) => {
              const authModel = ctx.get('authModel');
              const userId = ctx.apply('authModel.getUserId');
              return {
                userId,
                authModel,
              };
            }),
          },
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
